function _largura(width){return(
width/2
)}

function _map(largura,barChartSvg,d3,barChartRaceUpdate,get_bars_values,countryName,topojson,getCountryName)
{
  const height = 400;
  const width = largura
  const outer = barChartSvg/*d3.select(DOM.svg(width, height));*/
  const svg = outer.append('svg').attr('width', width).attr('height', height).attr('x', width/1.5); 

  const projection = d3.geoMercator().scale(90).translate([(width/2), height/1.6 + 40]);
  const path = d3.geoPath(projection);
  const g = svg.append('g');

  
  barChartRaceUpdate(get_bars_values(countryName));
  d3.json('https://cdn.jsdelivr.net/npm/world-atlas@2/countries-50m.json').then(data => {
    const countries = topojson.feature(data, data.objects. countries);
    g.selectAll('path').data(countries.features).enter().append('path')
      .attr('class', 'country')
      .attr('id', d => d.properties.name)
      .attr('d', path);

    
    g.selectAll('path').on("click", d => {
      getCountryName(d, countryName);
      const data = get_bars_values(countryName);
      barChartRaceUpdate(data); 
  })
    
    
  });

  
  return outer
  
}


function _getCountryName()
{

  function getCountryName(d,countryName) {
    countryName.value = d.path[0].__data__.properties.name;
  }
  return getCountryName;
}


function _get_bars_values(olympics)
{

  function get_values(country) {

    let SportMap = new Map();
    let SportAthletesCount = new Map();

    const obj = {"Gold": 3, "Silver": 2, "Bronze": 1};
    const MedalMetrica = new Map(Object.entries(obj));
  
    olympics.forEach(d => {

      if(d.region == country.value){
        if(d.Medal !== "No Medal"){
          if (!SportMap.has(d.Sport)){
            console.log(d.Medal, MedalMetrica.get(d.Medal));
            SportMap.set(d.Sport, + MedalMetrica.get(d.Medal));
          }
          else{
            SportMap.set(d.Sport, SportMap.get(d.Sport) + MedalMetrica.get(d.Medal));
          }
        }
        if (!SportAthletesCount.has(d.Sport)){
            SportAthletesCount.set(d.Sport, 1);
        }
        else{
            SportAthletesCount.set(d.Sport, SportAthletesCount.get(d.Sport) + 1);
        }

      }

    });
    
    let arr = Array.from(SportMap, ([key, value]) => {
      return {"Sport" : key, "count": (value/(SportAthletesCount.get(key) * 3)) * 100};
    });
    console.log(arr);  
    return arr.slice(0, 5);
  }
  
  return get_values
}


function _xScale(d3,largura){return(
d3.scaleLinear().domain([0,100]).range([0, largura * 0.8])
)}

function _g1(barChartSvg,d3,xScale)
{
  var xAxisTranslate = 30;
  const g = barChartSvg.append("g")
      .attr('transform', 'translate(50,' + xAxisTranslate + ')')
      .attr('style', 'color: white')
  var x_axis = d3.axisTop().ticks(20,undefined).scale(xScale); 


  

  return () => {g.call(x_axis);};
}


