import React, { useState } from "react";

import { TimeLine, TimeLineTitle, TimeLineBottom } from "./timeLine.js";
import StaticView from "./staticView.js";
import "./index.css";
import { YearContext } from "./year_context.js";
import MapBar from "../vis/map-bar/mapBar.js";
import BubblePlot from "../vis/bubble/bubblePlot.js";

//const YearContext = React.createContext();

const TimeLineVis = () => {
  const [year, setYear] = useState(1896);

  return (
    <YearContext.Provider value={{ year, setYear }}>
      <div className="container">
        <TimeLineTitle />
        <div className="container2">
          <TimeLine />
          <StaticView />
        </div>
        <div style={{ background: "#012030", margin: 10 }}>
          <br />
          <h1 style={{ color: "white" }}>
            Mapa e Grfico de Barras Interativo
          </h1>
          <br />
          <br />
          <p style={{ color: "white", fontSize: 20 }}>
            No painel abaixo  mostrado um mapa que interage com o grfico de
            barras ao lado quando um pas  selecionado. Para cada pas 
            mostrado a sua taxa de aproveitamento por esporte nas olimpadas.
            <br />
            Para o clculo da taxa de apoveitamento foram definidos pesos 1, 2 e
            3 para as medalhas de bronze, prata e ouro. Aps isso,  feito o
            somatrio das medatlas multiplicando-as pelos seus pesos
            correspondentes.
            <br />
            Por fim, vamos ter essa soma dividida pelo numero de atletas
            enviados pelo pas, gerando assim a taxa de aproveitamento.
          </p>
          <br />
          <br />
          <br />
          <br />
        </div>
        <div className="chart">
          <MapBar className="mapbar" />
        </div>
        <div style={{ background: "#012030", margin: 10 }}>
          <br />
          <h1 style={{ color: "white" }}>Grfico de Bolhas Interativo</h1>
          <br />
          <br />
          <p style={{ color: "white", fontSize: 20 }}>
            Aqui temos a visualizao de um grfico de bolhas em que seus eixos
            x e y correspondem, respectivamente, ao total de medalhas e o total
            de atletas enviados. Cada crculo no grfico representa um pas e
            sua rea representa o seu pib per cpita.
            <br />
            No grfico, a cada dcada, iniciando em 1890 at 2010, so
            representados os 15 pases que possuem o maior nmero de medalhas. O
            intuito da visualizao  ver a relao entre numero de atletas
            enviados, numero de medalhas ganhas e fatores economicos.
          </p>
        </div>
        <div className="chart">
          <BubblePlot className="mapbar" />
        </div>
        <TimeLineBottom />
      </div>
    </YearContext.Provider>
  );
};

export default TimeLineVis;
