function _get_modalities_by_sex()
{
  function a(year, data) {
    let ret = {"women": 0, "men": 0, "mixed":0}
    
    let wom = []
    let mm = []
    let mx = []
    
    data.forEach(d => {
      if (year == d.Year) {
        let words = d.Event;
        if (words.includes("Mixed") && !mx.includes(d.Event.toLowerCase().replace(/\s/g, ''))) {
          ret.mixed = ret.mixed + 1;
          mx.push(d.Event.toLowerCase().replace(/\s/g, ''));
        }
        else if (words.includes("Men's") && !mm.includes(d.Event.toLowerCase().replace(/\s/g, ''))) {
          ret.men = ret.men + 1;
          mm.push(d.Event.toLowerCase().replace(/\s/g, ''));
        }
        else if (words.includes("Women's") && !wom.includes(d.Event.toLowerCase().replace(/\s/g, ''))) {
          ret.women = ret.women + 1;
          wom.push(d.Event.toLowerCase().replace(/\s/g, ''));
        }    
      } 
    });
    return  [ret.women, ret.men, ret.mixed]
    //return ret; 
  }
  return a;
}


