function _get_qtd_by_sex()
{
  function a(year, data) {
    let ret = {"qtwomen": 0, "qtmen": 0}
    
    let id_w = []
    let id_m = []
    
    data.forEach(d => {
      if (year == d.Year) {
        if (d.Sex === "M" && !id_m.includes(d.ID)){
          ret.qtmen = ret.qtmen + 1;
          id_m.push(d.ID);
        }
        if (d.Sex === "F" && !id_w.includes(d.ID)){
          ret.qtwomen = ret.qtwomen + 1;
          id_w.push(d.ID);
        }
      } 
    });
    let total = ret.qtwomen + ret.qtmen
    ret = {"qtwomen": Math.round((ret.qtwomen/total) * 10000)/100, "qtmen": Math.round((ret.qtmen/total) * 10000)/100}
    return ret; 
  }
  return a;
}


