function _piechartmalefemale(svg,width,height,get_qtd_by_sex,olympics,d3)
{
  
  svg.append("circle").attr("cx",-320).attr("cy",-150).attr("r", 6).style("fill", "#C1666B")
  svg.append("circle").attr("cx",-320).attr("cy",-120).attr("r", 6).style("fill", "#8E7DBE")
  svg.append("circle").attr("cx",-320).attr("cy",-90).attr("r", 6).style("fill", "#F7C59F")
  svg.append("text").attr("x", -300).attr("y", -150).text("Feminina").style("font-size", "15px").attr("alignment-baseline","middle").attr("fill",   "white")
  svg.append("text").attr("x", -300).attr("y", -120).text("Masculina").style("font-size", "15px").attr("alignment-baseline","middle").attr("fill", "white")
  svg.append("text").attr("x", -300).attr("y", -90).text("Mista").style("font-size", "15px").attr("alignment-baseline","middle").attr("fill", "white")


 const generate = (year) => {


  const radius = Math.min(width, height) / 2 - 40

  const data = get_qtd_by_sex(year, olympics)
  
 // Set the color scale
 const color = d3.scaleOrdinal().domain([
       "qtdmen",
       "qtdwomen"
     ]).range(["#C1666B", "#8E7DBE"])
  
  // Compute the position of each group on the pie:
  const pie = d3.pie()
    .value(function(d) {return d[1]})
  const data_ready = pie(Object.entries(data))
   
  // Now I know that group A goes from 0 degrees to x degrees and so on.
  const arcGenerator = d3.arc()
    .innerRadius(0)
    .outerRadius(radius)
   
  const sex = svg
    .selectAll('mySlices')
    .data(data_ready)
    .join(
        function(enter) {
          
          return enter
            .insert('path')
            .transition()
            .attr('d', arcGenerator)
            .attr('fill', function(d){ return(color(d.data[0])) })
            .duration(500)
            .ease(d3.easeLinear)
            
            .attr("stroke", "#012030")
            .attr("class", "slice")
            .style("stroke-width", "2px")
            .style("opacity", 1.0)
            
        },
        function(update) {
          return update
          .select('path')
          .transition()
          .attr('d', arcGenerator)
          .duration(500)
          .ease(d3.easeLinear)
          .style("opacity", 0.0)
           
        },
        function(exit) {
          return exit.remove();
        }
      )

      sex.append("title").text(d => d.data[1] + " %");
 } 

  return generate

}


