function _barras1(get_modalities_by_sex,olympics,d3,svgBar,height2)
{

  //svgBar.append("text").attr("x", 75).attr("y", 40).text("Modalidades").style("font-size", "18px").attr("alignment-baseline","middle")
  
  // svgBar.append("circle").attr("cx",460).attr("cy",130).attr("r", 6).style("fill", "#C1666B")
  // svgBar.append("circle").attr("cx",460).attr("cy",160).attr("r", 6).style("fill", "#8E7DBE")
  // svgBar.append("circle").attr("cx",460).attr("cy",190).attr("r", 6).style("fill", "#F7C59F")
  // svgBar.append("text").attr("x", 480).attr("y", 130).text("Feminina").style("font-size", "15px").attr("alignment-baseline","middle").attr("fill", "white")
  // svgBar.append("text").attr("x", 480).attr("y", 160).text("Masculina").style("font-size", "15px").attr("alignment-baseline","middle").attr("fill", "white")
  // svgBar.append("text").attr("x", 480).attr("y", 190).text("Mista").style("font-size", "15px").attr("alignment-baseline","middle").attr("fill", "white")
    
  function update_svg_bar(year) {
    
    const dados = get_modalities_by_sex(year, olympics);

    const color = d3.scaleOrdinal().domain([
       0,
       1,
       2
     ]).range(["#C1666B", "#8E7DBE", "#F7C59F"])

    svgBar.selectAll('rect') // Seleciona todos os retngulos filhos de svg (nesse caso, uma seleo vazia)
      .data(dados) // Vincula arrayOfNumbers com DOM elementos <rect/>, produzindo selees .enter(),.exit()
      .join(
        function enter(enter) {
          return  enter
            .append('rect') // Para cada item de dado, adiciona um <rect /> ao svg selecionado
            .attr('x', (d, i) => i * 90 + 100) // Configura a posio x de acordo com o ndice do vetor
            .attr('width', 80) //Configura a largura de cada barra
            .attr('fill', (d, i) => color(i))
            .attr('y',  (d) => height2)
            .transition()
            .attr('y',  (d) => height2 - d*2)
            .attr('height', (d) => d*2)
            .duration(800) 
        },
        function update(update) {
           return update
            .transition()
            .attr('y',  (d) => height2 - d*2)
            .attr('height', (d) => d*2)
            .duration(800)
          }
      )

    svgBar
      .selectAll('.mdtxt_')
      .remove();
    
    svgBar.selectAll('label') // Seleciona todos os retngulos filhos de svg (nesse caso, uma seleo vazia)
      .data(dados) // Vincula arrayOfNumbers com DOM elementos <rect/>, produzindo selees .enter(),.exit()
      .join(
        function enter(enter) {
          return  enter
            
            .append('text')
            .attr('x', (d, i) => i * 90 + 100 + 40)
            .attr('y',  (d) => height2 - d*2 - 5)
            .transition()
            .ease(d3.easeBounce)
            .delay(800)
            // Para cada item de dado, adiciona um <rect /> ao svg selecionado
             // Configura a posio x de acordo com o ndice do vetor
            
            .text((d, i)  => d)
            .style('text-anchor', 'middle')
            .style('font-weight', 'bold')
            .attr("fill", "white")
            
        }
        
      )
      .attr("class", "mdtxt_")
  }

  
    
  
  return update_svg_bar;
}


