function _treemap(marginT,widthT,heightT,svgTreeMap,get_most_popular_sports,d3)
{

  
  const margin = marginT,
    width = widthT,
    height = heightT;

// append the svg object to the body of the page
  const svg = svgTreeMap
    .append("svg")
    .attr("width", width + margin.left + margin.right)
    .attr("height", height + margin.top + margin.bottom)
    .append("g")
    .attr("transform",
          `translate(${margin.left}, ${margin.top})`);

  function render(year) {
    const data = get_most_popular_sports(year);
  
    // stratify the data: reformatting for d3.js
    const root = d3.stratify()
      .id(function(d) { return d.Sport; })   // Name of the entity (column name is name in csv)
      .parentId(function(d) { return d.parent; })   // Name of the parent (column name is parent in csv)
      (data);
    root.sum(function(d) { return +d.count })   // Compute the numeric value for each entity

    // Then d3.treemap computes the position of each element of the hierarchy
    // The coordinates are added to the root object above
    d3.treemap()
      .size([width, height])
      .padding(4)
      (root)

    // use this information to add rectangles:
    svg
      .selectAll("rect")
      .data(root.leaves())
      .join("rect")
      .attr('x', function (d) { return d.x0; })
      .attr('y', function (d) { return d.y0; })
      .attr('width', function (d) { return d.x1 - d.x0; })
      .attr('height', function (d) { return d.y1 - d.y0; })
      .style("stroke", "black")
      .style("fill", "#C1666B");

    // and to add the text labels
    svg
      .selectAll("text")
      .data(root.leaves())
      .join("text")
      .attr("x", function(d){ return d.x0+10})    // +10 to adjust position (more right)
      .attr("y", function(d){ return d.y0+20})    // +20 to adjust position (lower)
      .text(function(d){ return d.data.Sport})
      .attr("font-size", "15px")
      .attr("fill", "white")
  }
  return render
}


function _get_most_popular_sports(olympics)
{
  function s(year) {
    let wom = new Map();
    
    olympics.forEach(d => {
      if (year == d.Year) {
        let sport = d.Sport;

        if (d.Sex === "F") {
          if (!wom.has(sport)){
            wom.set(sport, 1);
          }
          else{
            wom.set(sport, wom.get(sport) + 1);
          }
        } 
      } 
    });
    let arr = Array.from(wom, ([key, value]) => {
      return {Sport: key, count: value, parent: "Sem Informao"};
    });

    arr = arr.sort((d, a) => a.count - d.count)
    arr.unshift({Sport: "Sem Informao", count: null, parent: null})
    return arr.slice(0, 5); 
  }
  return s;
}


