import define1 from "./450051d7f1174df8@254.js";

function _1(md){return(
md`# Bubble Chart`
)}

function _datasets(require){return(
require('vega-datasets')
)}

function _gdppd(FileAttachment){return(
FileAttachment("limpados4.csv").csv({typed: true})
)}

function _widthBubble(width){return(
width
)}

function _hightBubble(){return(
500
)}

function _margin2(){return(
{top: 40, right: 40, bottom: 80, left: 40}
)}

function _x(d3,gdppd,margin2,widthBubble){return(
d3.scaleLinear()
  .domain([0, d3.max(gdppd, d => d.medals_total)])
  .range([margin2.left, widthBubble - margin2.right])
  //.range([1000, 0])
  .nice()
)}

function _y(d3,gdppd,hightBubble,margin2){return(
d3.scaleLinear()
  .domain([0, d3.max(gdppd, d => d.athelets_total)])
  .range([hightBubble - margin2.bottom, margin2.top])
  //.range([500, 0])
  .nice()
)}

function _years(d3,gdppd){return(
d3.extent(gdppd, d => d.year)
)}

function _dataInitial(gdppd,years){return(
gdppd.filter(d => d.year === years[0])
)}

function _color2(d3,gdppd){return(
d3.scaleOrdinal()
  .domain(gdppd.map(d => d.region))
  .range(d3.schemeTableau10)
)}

function _size(d3,gdppd){return(
d3.scaleSqrt()
  .domain(d3.extent(gdppd,  d => d.gdppc_by_year))
  .range([4, 60])
)}

function _a(chartDynamic,yearFilter){return(
chartDynamic.setYear(yearFilter)
)}

function _yearFilter(Scrubber,d3,years){return(
Scrubber(
  d3.range(years[0], years[1] + 1, 10), // min to max years in 5 year increments
  { autoplay: false, delay: 800, loop: false } // experiment with these settings!
)
)}

function _chartDynamic(d3,widthBubble,hightBubble,margin2,x,y,years,dataInitial,color2,gdppd,size)
{
  
  const svg = d3.create('svg')
    .attr('width', widthBubble)
    .attr('height', hightBubble);

  svg.append('g')
    .attr('transform', `translate(0, ${hightBubble - margin2.bottom})`)
    .call(d3.axisBottom(x))
    .style("color", "white")
    .append('text')
      .attr('text-anchor', 'end')
      .attr('fill', 'white')
      .attr('font-size', '12px')
      .attr('font-weight', 'bold')
      .attr('x', widthBubble - margin2.right)
      .attr('y', -10)
      .text('Total de medalhas');

  svg.append('g')
    .attr('transform', `translate(${margin2.left}, 0)`)
    .call(d3.axisLeft(y))
    .style("color", "white")
    .append('text')
      .attr('transform', `translate(20, ${margin2.top}) rotate(-90)`)
      .attr('text-anchor', 'end')
      .attr('fill', 'white')
      .attr('font-size', '12px')
      .attr('font-weight', 'bold')
      .text('Total de Atletas');
  
  const yearLabel = svg.append('text')
    .attr('class', 'year')
    .attr('x', widthBubble - 6*margin2.right)
    .attr('y', margin2.top + 20)
    .attr('fill', 'white')
    .attr('font-family', 'Helvetica Neue, Arial')
    .attr('font-weight', 500)
    .attr('font-size', 80)
    .text(years[0]);
  
  
  const countries = svg
    .selectAll('circle.region')
    .data(dataInitial, d => d.region)
    .join('circle')
      .sort((a, b) => b.gdppc_by_year - a.gdppc_by_year)
      .attr('class', 'region')
      .attr('opacity', 0.7)
      // .attr('fill', d => color2(d.country))
      // .attr('cx', d => x(d.fertility))  
      // .attr('cy', d => y(d.life_expect))
      // .attr('r', d => size(d.pop));
      .attr('fill', d => color2(d.region))
      .attr('cx', d => x(d.medals_total))  
      .attr('cy', d => y(d.athelets_total))
      .attr('r', d => 1);
  
  countries
    .append('title')
    .text(d => d.region);

  countries
    .on('mouseover', function() {
      d3.select(this).attr('stroke', '#333').attr('stroke-width', 2);
    })
    .on('mouseout', function() {
      d3.select(this).attr('stroke', null);
    });
  
  function setYear(year) {

    const test = gdppd.filter(d => d.year === year)
    //const test2 = gdppd.slice(0, 10);
    yearLabel.text(year);
    const top15 = test.sort((d,c) => c.medals_total - d.medals_total);
    const top15_sliced = top15.slice(0,15);
    countries
      //.data(gdppd.filter(d => d.year === year), d => d.country)
      .data(top15_sliced/*, d => d.region*/)
      .join(
        function(enter) {
          
          return enter
            .transition()
            .duration(1000)
            .attr('cx', d => x(d.medals_total)) 
            .attr('cy', d => y(d.athelets_total))
            .attr('r', d => size(d.gdppc_by_year))
            .select('title')
            .text(d => "Pas: " + d.region+ "\n" + "Qtd Medalhas: "+ d.medals_total + "\nQtd Atletas enviados: "+ d.athelets_total);
        },
        function(update) {
          return update
            .transition()
            .duration(1000)
            .attr('cx', d => x(d.medals_total)) 
            .attr('cy', d => y(d.athelets_total))
            .attr('r', d => size(d.gdppc_by_year))
            .select('title')
            .text(d => "Pas: " + d.region + "\n" + "Qtd Medalhas: "+ d.medals_total + "\nQtd Atletas enviados: "+ d.athelets_total+ "\n" + "PIBpc: "+ d.gdppc_by_year)
        },
        function(exit) {
          return exit.remove();
        }
      )
  }



  return Object.assign(svg.node(), { setYear });
}


function _css(html)
{
  
 return html`
<style type="text/css">

  svg {
    background-color: transparent;
  }
</style>
`
}


export default function define(runtime, observer) {
  const main = runtime.module();
  function toString() { return this.url; }
  const fileAttachments = new Map([
    ["limpados4.csv", {url: new URL("./files/5d693f807879117bfa0581594711d155c3704df389e15c4d6f55cacc8402bfb5e182b683150ab9bff76ab5de566ef0394a3b686172e3bc042bff0a884c67ba83.csv", import.meta.url), mimeType: "text/csv", toString}]
  ]);
  main.builtin("FileAttachment", runtime.fileAttachments(name => fileAttachments.get(name)));
  main.variable(observer()).define(["md"], _1);
  main.variable(observer("datasets")).define("datasets", ["require"], _datasets);
  main.variable(observer("gdppd")).define("gdppd", ["FileAttachment"], _gdppd);
  main.variable(observer("widthBubble")).define("widthBubble", ["width"], _widthBubble);
  main.variable(observer("hightBubble")).define("hightBubble", _hightBubble);
  main.variable(observer("margin2")).define("margin2", _margin2);
  main.variable(observer("x")).define("x", ["d3","gdppd","margin2","widthBubble"], _x);
  main.variable(observer("y")).define("y", ["d3","gdppd","hightBubble","margin2"], _y);
  const child1 = runtime.module(define1);
  main.import("Scrubber", child1);
  main.variable(observer("years")).define("years", ["d3","gdppd"], _years);
  main.variable(observer("dataInitial")).define("dataInitial", ["gdppd","years"], _dataInitial);
  main.variable(observer("color2")).define("color2", ["d3","gdppd"], _color2);
  main.variable(observer("size")).define("size", ["d3","gdppd"], _size);
  main.variable(observer("a")).define("a", ["chartDynamic","yearFilter"], _a);
  main.variable(observer("viewof yearFilter")).define("viewof yearFilter", ["Scrubber","d3","years"], _yearFilter);
  main.variable(observer("yearFilter")).define("yearFilter", ["Generators", "viewof yearFilter"], (G, _) => G.input(_));
  main.variable(observer("chartDynamic")).define("chartDynamic", ["d3","widthBubble","hightBubble","margin2","x","y","years","dataInitial","color2","gdppd","size"], _chartDynamic);
  main.variable(observer("css")).define("css", ["html"], _css);
  return main;
}
