import React, { useEffect } from "react";

import "./index.css";
import { YearContext } from "./year_context.js";
// const YearContext = React.createContext();

const TimeLineTitle = () => {
  return (
    <div className="time-line-title">
      <div className="header-image">
        <h1> 120 anos de Olimpadas</h1>

        <h4> Uma anlise dos jogos olmpicos de vero.</h4>
      </div>
    </div>
  );
};

const TimeLineBottom = () => {
  return (
    <div className="time-line-title">
      <div className="header-image"></div>
    </div>
  );
};

const TimeLineItem = ({ year, text, setVisibleYear }) => {
  //const [offset, setOffset] = useState(0);
  //const {yearState} = React.useContext(YearContext);
  const { setYear } = React.useContext(YearContext);
  useEffect(() => {
    const onScroll = () => {
      const element = document.getElementById(year);
      const rect = element.getBoundingClientRect();
      var style = element.currentStyle || window.getComputedStyle(element);
      //     var ul = document.getElementById("ulid").getBoundingClientRect();
      //    console.log(

      //     {
      //         "window" : window.innerHeight,
      //         "client" :  document.documentElement.clientHeight
      //     }
      //    )
      // var style = window.getComputedStyle(element);

      //console.log("Year", year, rect, parseFloat(style.paddingTop) + rect.top)
      const isVisible =
        rect.top + parseFloat(style.paddingTop) >= 0 &&
        rect.top <=
          (window.innerHeight || document.documentElement.clientHeight);
      // rect.bottom <= (window.innerHeight || document.documentElement.clientHeight)
      //&&
      // element.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&

      if (isVisible) {
        setYear(year);
        //setVisibleYear(year)
        console.log("Year: ", year, isVisible);
      }
    };
    // clean up code
    window.removeEventListener("scroll", onScroll);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  //console.log(offset);

  return (
    <>
      <li id={year}>
        <h1>{year}</h1>
        {text}
      </li>
    </>
  );
};

const TimeLine = () => {
  return (
    <div className="time-line">
      <h1>Timeline da trajetria feminina nos jogos olmpicos</h1>
      <ul id={"ulid"}>
        <TimeLineItem
          year={1896}
          text={
            "As Olimpadas Modernas foram reiniciadas pelo Baro de Coubertin, Pierre de Frdy, em 1896. Coubertin reproduziu as Olimpadas da Antiguidade na Modernidade, desta forma, a participao das mulheres permaneceu vedada, restando-lhes apenas a assistir s provas. Pierre de Frdy reconhecia o direito das mulheres quanto  educao esportiva, inclusive competindo entre elas. Porm, fora da vista dos homens por razes culturais, antropolgicas e fisiolgicas. Durante a primeira Olimpada em 1896, em Atenas, as mulheres participaram apenas como espectadoras, porm a grega Stamati Revithi, realizou o percurso da maratona fora do estdio no dia seguinte  prova masculina, em resposta a proibio feminina nas modalidades olmpicas. Ela realizou a corrida em quatro horas e meia, foi mais rpida que alguns homens, porm no foi reconhecida e provocou o incio da gradual insero das mulheres nos esportes olmpicos."
          }
        />
        <TimeLineItem
          year={1900}
          text={
            'Quatro anos depois, a sede das Olimpadas de 1900 foi em Paris, marcada na poca pelo liberalismo. Devido a lacunas e falta de organizao do COI no controle do programa dos jogos foi permitido  participao extraoficial de mulheres em algumas provas, o golfe e o tnis, por serem belos e no possuir contato fsico. Coubertin fez um leve recuo e autorizou que 22 mulheres participassem dos jogos em Paris, em 1900. Um nmero irrisrio dentro de um total de 997 atletas, 2,2% deles. E tem mais: das 19 prticas, apenas 5 eram permitidas a elas. Tnis, vela, croquet, hipismo e golfe. Atividades exclusivas da aristocracia e que, de acordo com a viso da poca, no demandam muito esforo fsico, tampouco "compromete" a feminilidade. Naquele ano, foram 22 atletas admitidas. A condessa Hlne de Pourtals, nascida nos Estados Unidos, foi a primeira a participar dos jogos, na competio de vela, em 22 de maio de 1900. Hlne representou a Sua com a tripulao do barco Lerina, que recebeu uma medalha de ouro. Hlne foi, portanto, a primeira a participar e a ganhar a medalha. J a tenista britnica Charlotte Cooper, foi a primeira a vencer em categoria individual.'
          }
        />
        <TimeLineItem
          year={1908}
          text={
            "No ano de 1908, em Londres, as provas eram tnis, patinao no gelo, arco e flecha. Na modalidade barco a vela, as equipes eram mistas, as mulheres do time eram esposas dos atletas. A arqueira britnica Queenie Newall se destacou em 1908  dentre 25 competidores  vencendo a medalha de ouro. Naquele ano, a presena de mulheres finalmente se tornou oficial, embora ainda muito restrita."
          }
        />
        <TimeLineItem
          year={1912}
          text={
            "Em Estocolmo, 1912, foi permitida a natao. Em 1916 no houve Olimpadas devido  primeira guerra mundial. Em 1917 foi a criao da Federao das Sociedades Esportivas Femininas da Frana. Alice Milliat foi uma das grandes expoentes por maior representatividade nas Olimpadas. Ela militava pela participao de mulheres nas modalidades de Atletismo (corrida, lanamentos e saltos), que ainda eram exclusivas a homens. Em 1917, ela e outros membros da Femina (clube desportivo para mulheres) fundaram a Federao das Sociedades Esportivas Femininas da Frana. "
          }
        />
        <TimeLineItem
          year={1920}
          text={
            "Em 1920, na Anturpia, foi includo o salto ornamental. Diante das recusas do Comit Olmpico Internacional (COI) em permitir a insero de mulheres nas demais modalidades, Alice Milliat decidiu dar um passo em outra direo e com Federao Internacional de Esportes Femininos (FSFI) fundou os Jogos Olmpicos Femininos, que teve quatro edies entre 1922 e 1934. No primeiro evento, 77 atletas tomaram parte representando Gr-Bretanha, Sua, Itlia, Noruega e Frana. Em um ato de resistncia e com mais de 15 mil espectadores, elas provaram que o argumento de Pierre no tinha fundamento. Alice era praticante do remo profissional, mas tambm se destacava no hquei e natao. Segundo o historiador Florence Carpentier, sua misso com os Jogos Mundiais Femininos era assumir o controle das competies femininas para escapar da tutela das federaes de atletismo. Seu objetivo era provar aos lderes do COI as capacidades esportivas das mulheres para serem admitidas em todo o programa olmpico."
          }
        />
        <TimeLineItem
          year={1928}
          text={
            "Em Amsterd, 1928, foram inseridas as modalidades, ginstica e atletismo, este ltimo com cinco provas. Ao final da prova de 800 metros algumas mulheres desmaiaram, assim surgiram argumentos que defendiam a excluso feminina das Olimpadas devido a sua incapacidade fsica para provas de resistncia. Trs anos aps a sada de Pierre de Coubertin do Comit Internacional Olmpico, pela primeira vez na histria das Olimpadas, mulheres passaram a competir nas modalidades de corrida de velocidade (100 metros, 400 metros, 800 metros) e salto em altura. Desde os primeiros jogos, homens competem na categoria de 800 metros, em 1928 foi a primeira vez para as mulheres. Nos jogos seguintes, foram impedidas novamente."
          }
        />
        <TimeLineItem
          year={1936}
          text={
            "Nas Olimpadas de 1932, em Los Angeles, no foi realizada a prova de 800 metros. Em 1936, em Berlim, as mulheres foram includas oficialmente como atletas olmpicas, assim a FEFI foi dissolvida, pois conquistou seu objetivo."
          }
        />
        <TimeLineItem
          year={1972}
          text={
            "Tratado internacional criado pela Assembleia Geral das Naes Unidas sobre os direitos das mulheres, assinado por 188 pases. Uma das premissas era garantir as mesmas oportunidades para participar ativamente nos esportes e na educao fsica."
          }
        />
        <TimeLineItem
          year={1984}
          text={
            "Pela primeira vez, duas mulheres passaram a integrar o Comit Internacional Olmpico: a ex-atleta venezuelana Flor Isava Fonseca, que fazia parte do time equestre, e a ex-velocista finlandesa Pirjo Haeggman."
          }
        />
        <TimeLineItem
          year={1992}
          text={
            "A partir do ano de 1991, todos os esportes que fossem includos deveriam permitir que mulheres competissem. A regra, entretanto, no torna mandatria a adaptao nas categorias antigas. Alguns exemplos das novas prticas foram halterofilismo, pentatlo moderno, boxe, taekwondo, triatlo, salto de esqui e trampolim.Em 1995, a Declarao e Plataforma de Ao de Pequim criada na IV Conferncia das Naes Unidas sobre a Mulher, evento que tinha como previso fazer uma anlise dos obstculos a serem superados para que as mulheres possam exercer seus direitos e alcanar seu desenvolvimento integral como pessoas. Algumas das recomendaes falam sobre a sub-representao feminina em cargos de direo nos esportes, assim como a necessidade de programas que apoiem a participao de meninas e mulheres em atividades esportivas."
          }
        />
        <TimeLineItem
          year={2012}
          text={
            "Nos jogos de 2012, em Londres, pela primeira vez, todos os comits nacionais enviaram mulheres atletas em suas delegaes. Nos jogos de vero daquele ano foi tambm a primeira vez que mulheres competiram em todos os esportes  isso ocorreu porque o beisebol, exclusivamente masculino, foi retirado da programao."
          }
        />
      </ul>
    </div>
  );
};

export { TimeLine, TimeLineTitle, TimeLineBottom };
